<!-- eslint-disable -->
<template>
  <div>
    <div v-if="loading">
      <img src="../images/preloader.gif" alt="">
    </div>
    <div v-else-if="loadingFailed">Error</div>
    <router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}" v-else>
      <svg width="30" height="21" fill="currentColor">
        <use xlink:href="#icon-cart"></use>
      </svg>
      <span class="header__count" aria-label="Количество товаров">{{ $store.state.cartProducts.length }}</span>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState({ loading: 'cartLoading', loadingFailed: 'cartLoadingFailed' }),
  },
};
</script>
