<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}" >
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }}
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="categoryColor in colorsArr" :key="categoryColor.index">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="categoryColor" v-model="checked">
          <span class="colors__value" :style="`background-color: ${categoryColor}`">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import gotoPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      checked: '',
    };
  },
  filters: {
    numberFormat,
  },
  methods: {
    gotoPage,
  },
  computed: {
    colorsArr() {
      const colorsArr = this.product.colors;
      return colorsArr.map((colors) => colors.code);
    },
  },
  props: ['product'],
};
</script>
